  .mymodal{
    margin-top: 10rem;
    max-width: 80%;
    margin-left: 10%;
  }
  .m-header{
    background-color:#ffca3a;
  }
  .m-title{
    color: #000;
    margin: auto;
  }
  
  .modal-content {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-clip: padding-box;
    border-radius: 0.3rem;
}